function sliderInfo() {
    var swiper = new Swiper('.info .swiper', {
        slidesPerView: '3',
        spaceBetween: 8,
        pagination: {
            el: ".info .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
            },
            577: {
                slidesPerView: '3',
            },
        }
    })
}


$(document).ready(function() {
    sliderInfo();

    // Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__right').toggleClass('header__right--active')
        $('.menu-bg').toggleClass('menu-bg--active')
        $('body').toggleClass('fixed-body')
    })

    $('.header__list li').click(function () {
        $('.burger').removeClass('burger--active')
        $('.header__right').removeClass('header__right--active')
        $('.menu-bg').removeClass('menu-bg--active')
        $('body').removeClass('fixed-body')
    })

    $('.menu-bg').click(function () {
        $('.burger').removeClass('burger--active')
        $('.header__right').removeClass('header__right--active')
        $('.menu-bg').removeClass('menu-bg--active')
        $('body').removeClass('fixed-body')
    })

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})